import musicIcon from '@/components/music-icon/myesic-icon.vue'
import * as player from '../../utils/music/play.js'
export default {
	data() {
		return {
			list:[],
			bg:''
		}
	},
	methods: {
		async searchItemPlay(item){
			const result = await player.playMusic(item);
			if(result != "success"){
				uni.showToast({
					title:"收费歌曲",
					icon:"none"
				})
			}else{
				uni.showToast({
					title:"播放成功",
					icon:"none"
				})
			}
			
		},
		playAll(){
			if(this.list.length != 0){
				this.$store.commit('setMusicList',this.list);
				this.searchItemPlay(this.list[0])
			}else{
				uni.showToast({
				    title: '暂无歌曲',
				    duration: 2000
				});
			}
		
		}
				
	},
	onLoad(option) {
		console.log(option);
		this.bg = `https://y.gtimg.cn/music/photo_new/T001R300x300M000${option.id}.jpg?max_age=2592000`,
		this.$Api.getSingerDeatil(option.id).then(res=>{
			console.log(res);
			this.list = res;
		})
	},
	components:{
		musicIcon
	}
}
